@import "https://fonts.googleapis.com/css2?family=Josefin+Sans:wght@400;700&display=swap";
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  text-align: center;
  min-height: 100vh;
  font-family: Josefin Sans, sans-serif;
  font-size: 18px;
  font-weight: 500;
  position: relative;
}

.light--mode-body {
  color: #484b6a;
  background-color: #e4e5f1;
}

.dark--mode-body {
  color: #cacde8;
  background-color: #161722;
}

nav {
  padding: 6rem 0 2rem;
}

.input-box input {
  width: 100%;
  border-radius: .3rem;
  outline: none;
  margin: 2rem 0 .5rem;
  padding: .5rem .5rem .5rem 3rem;
  font-weight: 500;
}

.light--mode-input {
  color: #484b6a;
  background-color: #fafafa;
  border: 0 solid #fafafa;
}

.dark--mode-input {
  color: #cacde8;
  background-color: #25273c;
  border: 0 solid #25273c;
}

.nav-items, .todo-body, nav {
  margin: 0 auto;
}

.logo p {
  text-transform: uppercase;
  color: #fff;
  letter-spacing: 7px;
  font-size: 3rem;
  font-weight: 700;
}

.toggle {
  align-items: center;
}

.todo-body, nav {
  width: 50%;
}

.todo-body {
  margin-top: -20px;
}

.todo-box, .todo-box-filter {
  text-align: left;
  color: #cacde8;
  background-color: #25273c;
  border: 0 solid #25273c;
  border-bottom: 1px solid #777a92;
  padding: .7rem .7rem .7rem 2rem;
}

.light--mode-todo-box, .light--mode-todo-box-filter {
  color: #484b6a;
  background-color: #fafafa;
  border: 0 solid #fafafa;
  border-bottom: 1px solid #9394a5;
}

.dark--mode-todo-box, .dark--mode-todo-box-filter {
  color: #cacde8;
  background-color: #25273c;
  border: 0 solid #25273c;
  border-bottom: 1px solid #777a92;
}

.todo-box span {
  margin-left: .7rem;
}

.todo-box-filter {
  border-bottom: none;
}

.draggable-list .todo-box.over.draggable {
  background-color: #cacde8;
}

.toggle, .todo-box__sorter, .todo-sorter, .nav-items {
  display: flex;
}

.todo-sorter {
  width: 30%;
}

.todo-box__sorter, .todo-sorter {
  justify-content: space-around;
}

.todo-box {
  justify-content: space-between;
  display: flex;
}

.task_close {
  margin-right: 1rem;
}

.task_close:hover {
  cursor: pointer;
}

.hidden {
  visibility: hidden;
  display: none;
}

.nav-items {
  justify-content: space-between;
}

footer {
  margin: 1rem auto .5rem;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
}

.toggle img, .filter:hover {
  cursor: pointer;
}

.light--mode-filter:hover {
  color: #9394a5;
}

.dark--mode-filter:hover {
  color: #fff;
}

@media (max-width: 1130px) {
  .todo-body, nav {
    width: 80%;
  }
}

@media (max-width: 740px) {
  .sorters {
    width: 80%;
    margin: 3.5rem auto 0;
    padding: .5rem 0;
    position: absolute;
    left: 0;
    right: 0;
  }

  .light--sorters {
    color: #484b6a;
    background-color: #fafafa;
    border: 0 solid #fafafa;
  }

  .dark--sorters {
    color: #cacde8;
    background-color: #25273c;
    border: 0 solid #25273c;
  }
}

@media (max-width: 375px) {
  body {
    font-size: 13px;
  }

  .input-box input {
    padding: .5rem .5rem .5rem 1rem;
  }

  .logo p {
    text-transform: uppercase;
    color: #fff;
    letter-spacing: 7px;
    font-size: 2.4rem;
    font-weight: 700;
  }
}

.attribution {
  text-align: center;
  margin-top: .4rem;
  font-size: 11px;
}

.attribution a {
  color: #3e52a3;
}

